import React, { Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from 'pages/Home';
import NotFound from 'pages/NotFound';
import ThemeButton from 'styledComponents/ThemeButton.css'
import Loader from 'helpers/Loader';
const Skills = React.lazy(() => import('pages/Skills'));
const ListOfGoals = React.lazy(() => import('list-of-goals/App'));

const Content = ({ setTheme, isLightTheme }) => {
  console.log(`invoke Content`);

  const changeTheme = () => setTheme(theme => !theme);

  return (
    <>
      <Suspense fallback={<Loader isLightTheme={isLightTheme} />}>
        <Switch>
          <Route path='/' exact component={Home} />
          <Route path='/about' />
          <Route path='/skills' component={Skills} />
          <Route path='/learning' />
          <Route path='/contact' />
          <Route path='/listOfGoals' component={ListOfGoals} />
          <Route component={NotFound} />
        </Switch>
      </Suspense>
      <ThemeButton isLightTheme={isLightTheme}>
        <p>Change theme</p>
        <button className='theme-button' onClick=
          {changeTheme}>
          <div className='circle'></div>
        </button>
      </ThemeButton>
    </>
  )
};

export default React.memo(Content);