import styled from 'styled-components'

const Navigation = styled.div`
  .list {
    display: flex;
    padding: 0;
    justify-content: space-around;
    &__item {
      text-align: center;
      border-right: 1px solid;
      flex-basis: 20%;
      text-decoration: none;
      color: black;
      &:focus {
        outline: none;
      }
      &:last-child {
        border: none;
      }
    }
    .home {
    color:  blue;
    }
    .about {
    color:  #2A7D4B;
    }
    .skills {
    color:  rebeccapurple;
    }
    .learning {
    color:  chocolate;
    }
    .contact {
    color:  #140E62;
    }
  }
`

export default Navigation;